import React, { PureComponent } from 'react';
import { Col, Row } from 'reactstrap';

import {
  ServiceKindFilters,
  ServiceKindDictionary,
} from '../../../containers/index';

class Page extends PureComponent {
  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col>
            <ServiceKindFilters />
          </Col>
        </Row>
        <Row>
          <Col>
            <ServiceKindDictionary />
          </Col>
        </Row>
      </div>
    );
  }
}

export default Page;



